@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
    font-family: "Roboto Condensed", sans-serif;
}

.img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 50% 50%;
}

.tile {
    width: 250px;
    border-style: solid;
    border-width: thin;
    border-color: #adadad;
    box-shadow: 2px 2px #cccccc;
    border-radius: 2px;
    overflow: hidden;
}

.disabled-tile {
    width: 200px;
    border-style: solid;
    border-width: thin;
    border-color: #adadad;
    box-shadow: 2px 2px #cccccc;
    border-radius: 2px;
    overflow: hidden;
    opacity: 0.3;
}

.legends {
    display: flex;
    flex-direction: row;
}

.txt {
    text-align: justify;
    padding-left: 5px;
    padding-right: 5px;
}

.date {
    padding: 5px;
    font-weight: 900;
}

.truncate {
    overflow: hidden;
    line-height: 1.2em;
    height: 3.6em;
}

.star-disabled {
    height: 20px;
    width: 20px;
}

.circle-with-text-disabled {
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    text-align: center;
    margin: 5px 2px;
    font-size: 12px;
    padding: 8px;
    display: flex;
    height: 8px;
    width: 8px;
}

.pads {
    padding-left: 5px;
    padding-right: 5px;
}

.link {
    font-size: medium;
    font-weight: 900;
}

.display {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.card-container {
    width: 30%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.btn-container {
    width: 5%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.btn {
    outline: none;
    text-decoration: none;
    background-color: #e0d2f2;
    border: none;
    width: 3rem;
    padding-top: 5px;
    padding-bottom: 3px;
    border-radius: 5px;
    margin: auto;
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (max-width: 980px) {
    .dis {
        display: none;
    }
    .card-container {
        width: 80%;
    }
    .btn-container {
        width: 10%;
    }
}

.post-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 50% 50%;
}

.circle-with-text {
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    text-align: center;
    margin: 5px 2px;
    font-size: 15px;
    padding: 10px;
    display: flex;
    height: 10px;
    width: 10px;
}

.legends-div {
    display: flex;
    flex-direction: row;
}

.first-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

span {
    font-size: 20px;
}

.c {
    background-color: #fbe4e4;
}

.dc {
    background-color: #ddedea;
}
.pr {
    background-color: #ddebf1;
}

.cu {
    background-color: #f4dfeb;
}

.hc {
    background-color: #f4dfeb;
}

.star {
    height: 24px;
    width: 24px;
}

.star-div {
    margin-top: 8px;
    margin-left: 5px;
}

@media only screen and (max-width: 800px) {
    .star {
        height: 10px;
        width: 10px;
    }
    span {
        font-size: 15px;
    }

    .legends-div {
        height: 30px;
    }
    .circle-with-text {
        margin: 5px 2px;
        font-size: 10px;
        padding: 5px;
        height: 7px;
        width: 7px;
    }
}

@media only screen and (max-width: 1040px) {
    .star {
        height: 16px;
        width: 16px;
    }
    .star-div {
        margin-top: 12px;
    }
}

@media only screen and (max-width: 580px) {
    .star {
        height: 8px;
        width: 8px;
    }
    .star-div {
        margin-top: 16px;
    }
    .circle-with-text {
        margin: 3px 1px;
        font-size: 10px;
        padding: 3px;
        height: 8px;
        width: 8px;
    }
    .legends-div {
        margin-top: 5px;
    }
}

@media only screen and (max-width: 375px) {
    .circle-with-text {
        margin: 3px 1px;
        font-size: 7px;
        padding: 3px;
        height: 8px;
        width: 8px;
    }
    .star {
        height: 6px;
        width: 6px;
    }
}

.header {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: #f7f7f7;
    flex-direction: row;
    flex-wrap: nowrap;
    padding-top: 5px;
    padding-bottom: 5px;
    height: 40px;
    border-bottom: 1px solid #777777;
}

.calc-header {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    background-color: #f7f7f7;
    flex-direction: row;
    flex-wrap: nowrap;
    padding-top: 5px;
    padding-bottom: 5px;
    height: 40px;
    border-bottom: 1px solid #777777;
}

.header-txt {
    font-size: 150%;
    padding-top: 5px;
    padding-left: 10px;
}

.btn-today:hover {
    transform: scale(1.2);
}

.calendar-row {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.calendar-col {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
}

.all-calendar {
    display: flex;
    flex-direction: column;
}

.all-calendar-reverse {
    display: flex;
    flex-direction: column-reverse;
}

.btn-today {
    height: 2rem !important;
    margin-top: 5px;
    background-color: #fcfeff;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 15px;
    margin-right: 20px;
    border: solid 1px;
    border-color: #adadad;
    border-radius: 4px;
}

* {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    padding: 0;
}

